<template>
  <v-container grid-list-md>
  <v-card>
    <v-btn
              absolute
              dark
              fab
              top
              right
              small
              color="blue"
              @click="refreshData"
            >
              <v-icon>refresh</v-icon>
            </v-btn>
    <v-card-title>
      <v-dialog v-model="dialogDelete" max-width="490">
        <v-card>
          <v-card-title class="headline">Are you sure to delete user {{ editedItem.name }}?</v-card-title>
          <v-card-text>This action cannot by undo.</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" text @click="dialogDelete = false">Disagree</v-btn>
            <v-btn color="red darken-1" text @click.native="deleteItem" @click="dialogDelete = false">Agree</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-dialog v-model="dialog" max-width="700px">
        <template v-slot:activator="{ on }">
          <v-btn
            color="primary"
            dark
            v-on="on"
          >
            New group
          </v-btn>
        </template>
      <v-card>
        <v-form ref="form" v-model="valid" lazy-validation>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12 sm6 md4>
                <v-text-field label="Group name" :rules="nameRules" v-model="editedItem.name" required></v-text-field>
              </v-flex>
              <v-flex xs12 sm12 md12>
                <v-autocomplete
                v-model="editedItem.abilities"
                :disabled="isUpdating"
                :items="abilitiesId"
                chips
                color="blue-grey lighten-2"
                label="Abilities"
                item-text="name"
                item-value="id"
                multiple
              >
                <template
                  slot="selection"
                  slot-scope="data"
                >
                  <v-chip
                    :input-value="data.selected"
                    close
                    class="chip--select-multi"
                    @click:close="removeAbility(data.item)"
                  >
                    {{ data.item.name }}
                  </v-chip>
                </template>
                <template
                  slot="item"
                  slot-scope="data"
                >
                  <template >
                    <v-list-tile-content v-text="data.item.name"></v-list-tile-content>
                  </template>
                </template>
              </v-autocomplete>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click.native="close">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click.native="save" :disabled="!valid" >Save</v-btn>
        </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
    <v-spacer></v-spacer>
      <v-text-field
        append-icon="search"
        :label="$t('actions.search')"
        single-line
        hide-details
        v-model="search"
      ></v-text-field>
    </v-card-title>
    <v-data-table
      v-if="items"
      :headers="headers"
      :items="items"
      :search="search">
      <template v-slot:item.abilities="{ item }">
        <span v-for="element in item.relationships.abilities">
          <v-chip small>{{ element.name }}</v-chip>
        </span>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-btn icon class="mx-0" @click="editItem(item)">
          <v-icon color="teal">edit</v-icon>
        </v-btn>
        <v-btn icon class="mx-0" @click="deleteDialog(item)">
          <v-icon color="pink">delete</v-icon>
        </v-btn>
      </template>
      <template slot="no-data">
        <v-btn color="primary" @click="initialize">Reset</v-btn>
      </template>
    </v-data-table>
  </v-card>
  </v-container>
</template>

<script>

  export default {
    name: 'groups',
    data() {
      return {
        search: '',
        pagination: {
          sortBy: 'state',
          rowsPerPage: 10
        },
        dialog: false,
        dialogDelete: false,
        show3: false,
        checkbox: false,
        headers: [
          {
            text: 'id',
            align: 'left',
            value: 'id',
            width: '1%'
          },
          {
            text: 'name',
            align: 'left',
            value: 'name'
          },
          {
            text: 'abilities',
            align: 'left',
            value: 'abilities',
            sortable: false
          },
          {
            text: 'Actions',
            value: 'actions',
            sortable: false
          }
        ],
        valid: false,
        nameRules: [
          v => !!v || 'Name is required',
          v => (v && v.length <= 20) || 'Name must be less than 20 characters'
        ],
        editedIndex: -1,
        editedItem: {
          id: '',
          abilities: ''
        },
        defaultItem: {
          name: '',
          abilities: ''
        }
      };
    },
    computed: {
      formTitle() {
        return this.editedIndex === -1 ? 'New Group' : 'Edit Group';
      },
      formNew() {
        return this.editedIndex === -1;
      },
      items() {
        // console.log(this.$store.getters.groupsTableData);
        return this.$store.getters.groupsTableData;
      },
      abilitiesId() {
        const abilities = this.$store.getters.abilitiessTableData;
        return abilities.map(ability => ({
          name: ability.name,
          id: ability.id
        }));
      }
    },
    watch: {
      dialog(val) {
        return val || this.close();
      }
    },
    methods: {
      removeAbility(item) {
        // const index = this.editedItem.abilities.indexOf(item.value);
        const index = this.editedItem.abilities.findIndex(x => x.id === item.id);
        // console.log(index);
        if (index >= 0) this.editedItem.abilities.splice(index, 1);
        // console.log(this.editedItem.abilities);
      },
      editItem(item) {
        this.editedIndex = this.items.indexOf(item);
        // this.editedItem = Object.assign({}, item);
        this.editedItem.id = item.id;
        this.editedItem.name = item.name;
        this.editedItem.abilities = item.relationships.abilities.map(ability => ({
          id: ability.id
        }));
        this.dialog = true;
      },
      deleteDialog(item) {
        this.editedIndex = this.items.indexOf(item);
        this.editedItem = Object.assign({}, item);
        this.dialogDelete = true;
        // this.deleteItem(item);
      },
      deleteItem() {
        if (this.editedIndex > -1) {
          const item = this.editedItem;
          console.log(item);
          if (item.name !== 'admin') { // use for demo
          // if (!item.admin) {                          // use for production
            const index = this.items.indexOf(item);
            this.items.splice(index, 1);
            this.$store.dispatch('deleteGroup', item.id);
            setTimeout(() => {
              this.$store.dispatch('fetchGroups');
            }, 500);
          } else {
            console.log('Delete this group is not allowed');
          }
        }
      },
      close() {
        this.dialog = false;
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem);
          this.editedIndex = -1;
        }, 300);
      },
      save() {
        if (this.$refs.form.validate()) {
          if (this.editedIndex > -1) {
            Object.assign(this.items[this.editedIndex], this.editedItem);
            console.log(this.editedItem);
            console.log(this.items);
            this.$store.dispatch('updateGroup', this.editedItem);
            setTimeout(() => {
              this.$store.dispatch('fetchGroups');
            }, 500);
          } else {
            this.items.push(this.editedItem);
            this.$store.dispatch('createGroup', this.editedItem);
            setTimeout(() => {
              this.$store.dispatch('fetchGroups');
            }, 500);
          }
          this.close();
        }
      },
      refreshData() {
        this.$store.dispatch('fetchGroups');
        this.$store.dispatch('fetchAbilities');
      }
    },
    mounted() {
      this.$store.dispatch('fetchGroups');
      this.$store.dispatch('fetchAbilities');
    }
  };
</script>



